.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.button {
  padding: 10px 20px;
  border-radius: 50px; /* Adjust this value to control the roundness */
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.background {
  background-color: #d9d2e9;
}

@font-face {
  font-family: 'Inter';
  font-style: NORMAL;
  src: url(./Inter/Inter-VariableFont_slnt\,wght.ttf) format('truetype');
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
